img {
    width: 100%;
}
.profileInfoContainer {
    display: flex;
}
.contacts {
    flex-grow: 2;
}
.profileInfo ul li{
list-style: none;
}

.addPhotoButtonContainer {
    position: absolute;
    top: 0px;
    width: 100%;
    background-color: rgba(180, 158, 240, 1);
    padding: 5px 0px 5px 0px;
    display: none;
}
.addPhotoButtonContainer input {
    color: white;
    margin-left: 5px;
}
.profilePictureContainer {
    position: relative;
    width: 200px;
    cursor: pointer;
}